import React from "react";
import "./PartnerDetails.css"
import YitPage from "../../Components/YitPage/YitPage";
import YitComponent from "../../Structures/YitComponent";
import {GoogleMap, LoadScript} from "@react-google-maps/api";
import TabsWidget from "../../Components/TabsWidget/TabsWidget";
import MaterialTable from '@material-table/core';
import tableIcons from "../../Components/tableIcons";
import StationDetails from "../Stations/StationDetails/StationDetails";
import Chip from "@mui/material/Chip";
import {Edit,  QrCode2Outlined} from "@mui/icons-material";
import PartnerEditForm from "./PartnerEditForm/PartnerEditForm";
import CodeGeneratorForm from "./CodeGeneratorForm/CodeGeneratorForm";
const MuiIcons = require("@mui/icons-material")
const PartnerStations = ({data, icons}) => {

    let columns = [
        {title: 'SystemId', field: 'systemId'},
        {title: 'ID', field: 'id'},
        {
            title: 'Stat', field: 'stat',
            lookup: {
                0: <Chip label="Active" color={"primary"} style={{width: 120}}/>,
                1: <Chip variant={"outlined"} label="Offline" style={{width: 120, color: "red"}}/>
            }
        },
    ]
    return (
        <MaterialTable
            style={{height: "100%"}}
            components={
                {
                    Container: props => (
                        <div style={
                            {
                                position: "relative", width: '100%', height: "100%",
                                overflow: "auto"
                        }}>
                            {props.children}
                        </div>
                    ),

                }
            }
            icons={icons}
            title={""}
            columns={columns}
            data={data}
            localization={{body: {editRow: {deleteText: 'are you sur you wanna delete this item '}}}}
            actions={[
                {
                    icon: () => <MuiIcons.Block style={{color: "darkred"}}/>,
                    tooltip: 'Delete',

                },
                {
                    icon: () => <MuiIcons.Delete style={{color: "darkred"}}/>,
                    tooltip: 'Delete',
                },
                (rowData) => {
                    return {
                        icon: () => <MuiIcons.Launch
                            style={{color: "black"}}
                            onClick={()=>{
                                this.currentPage.current.mountBackDrop(
                                    <StationDetails id={rowData.id}></StationDetails>
                                )
                            }
                            }
                        />,
                        tooltip: "Details",
                    }
                }
            ]}
            options={{
                actionsColumnIndex: -1,
                exportButton: true,
                toolbar: false,
            }}
        />
    )
}

class PartnerDetails extends YitComponent {
    constructor(props) {
        super(props);
        this.state =
            {
                isLoading: true,
                failedLoading: true,
                currentPartner: {Stations : {}},
            }

    }

    async loadPrimaryData() {
        await this.loadCurrentPartner(this.props.id)
        if(this.state.failedLoading){

            this.currentPage.current.setState({
                failedLoading: true
            })
        }else {

            this.currentPage.current.setState({
                failedLoading: false,
                isLoading: false
            })
        }
    }

    async loadCurrentPartner(id) {
        let getOneOp = await this.MODELS._Partner.getOnById(id)
        if (getOneOp.finalResult === true) {

            this.setState({
                failedLoading: false,
                currentPartner: getOneOp.result,
            })

        } else {
            this.setState({
                failedLoading : true
            })
        }
    }

    async componentDidMount() {
        await this.loadPrimaryData()

    }

    render() {
        return (
            <YitPage
                ref={this.currentPage}
                customClass={"PartnerDetails"}
                customId={"PartnerDetailsPage"}
                title={"Partner Details"}
                topOperations={[
                    {
                        text:"Print",
                        icon: <QrCode2Outlined fontSize={"medium"}></QrCode2Outlined>,
                        onClick: ()=>{
                            this.currentPage.current.mountBackDrop(
                                <CodeGeneratorForm partner={this.state.currentPartner}/>
                            )
                        }
                    },
                    {
                        text: "Edit",
                        icon: <Edit fontSize={"medium"} />,
                        onClick: ()=>{
                            this.currentPage.current.mountBackDrop(
                                <PartnerEditForm partner={this.state.currentPartner}/>
                            )
                        }
                    }
                ]}
                reloadPrimaryInfo={()=>{this.loadPrimaryData().then()}}
            >
                <TabsWidget
                    tabsList={[
                        {title: "Global Information"},
                        {title: "Stations"},
                        {title: "Transactions"},
                    ]}
                >
                    <div id={"GlobalInfoHolder"}>
                        <div className="globalInformationHolder">
                            <b>Name:</b><br/>
                            {this.state.currentPartner.fullName}
                            <br/><br/>
                            <b>Total affected stations:</b><br/>
                            {this.state.currentPartner.Stations.length}
                            <br/><br/>
                            <b>Phone:</b><br/>
                            {this.state.currentPartner.phone}
                            <br/><br/>
                            <b>Current Stat:</b><br/>
                            {this.state.currentPartner.stat}
                        </div>
                        <div className="mapHolder">
                            <LoadScript
                                googleMapsApiKey="AIzaSyB3zO_aU97QjkN-p1MUofYobc2QNCf3y2M"
                            >
                                <GoogleMap
                                    mapContainerStyle={{width: "100%", height: "100%"}}
                                    center={{
                                        lat: this.state.currentPartner.x,
                                        lng: this.state.currentPartner.y
                                    }}
                                    zoom={10}
                                >
                                    { /* Child components, such as markers, info windows, etc. */}
                                    <></>
                                </GoogleMap>
                            </LoadScript>
                        </div>
                    </div>
                    <>
                        <PartnerStations
                            data={this.state.currentPartner.Stations}
                            icons={tableIcons}
                        ></PartnerStations>
                    </>
                    <></>
                </TabsWidget>
            </YitPage>

        )
    }
}

export default PartnerDetails