import React from 'react';
import './YitDataTable.css'
import MaterialTable from '@material-table/core';
import tableIcons from "../tableIcons"
import Button from "@material-ui/core/Button";


class YitDataTable extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            page: 0,
            totalCount: 0,
            searchEntry: this.props.searchEntry !== undefined ? this.props.searchEntry : "id"
        }
        const urlParams = new URLSearchParams(window.location.search);
        this.tableRef = React.createRef();

    }



    async componentDidMount() {
        console.clear()
        console.log(this.tableRef.current)
    }

    async loadTotal(){

    }

    async loadData(query){
        let offset = query.page*query.pageSize, limit =  query.pageSize
        let getAllOp

        if(query.search === ""){
            getAllOp = await this.props.dataModel.getAll(offset, limit)
        }
        else{
            getAllOp = await this.props.dataModel.searchBy(this.state.searchEntry, query.search)
        }

        if(getAllOp.finalResult){
            let result = {
                data : getAllOp.result.rows,
                total: getAllOp.result.count
            }

            return result
        }else {
            return false
        }
    }

    componentWillUnmount() {

    }


    render() {
        return (
            <MaterialTable
                style={{ minHeight: "100%", fontFamily: "Poppins"}}
                tableRef={this.tableRef}
                icons={tableIcons}
                title={""}
                data={query => new Promise((resolve, reject) => {
                        this.loadData(query)
                            .then(result => {
                                if(result == false){
                                    reject()
                                }else {
                                    resolve({
                                        data: result['data'],
                                        page: query.page,
                                        totalCount: result['total'],
                                    })
                                }

                            })
                    })}
                options={{
                    actionsColumnIndex: -1, exportButton: true, paginationPosition: "top",
                    pageSizeOptions: [5, 10, 20, 50, this.state.totalCount],
                }}
                localization={{
                    error:
                        <div className={"errorContainer"}>
                            <span>Data could not be Loaded</span>
                            <br/><br/>
                            <Button
                                style={{backgroundColor: "red", opacity: 20}}
                                color={"primary"}
                                variant={"contained"}
                                onClick={()=>{
                                    this.tableRef.current.onQueryChange({});
                                }}
                            >
                                Retry
                            </Button>
                        </div>
                }}
                {...this.props}
            >
            </MaterialTable>
        )
    }
}

export default YitDataTable