import React from "react";
import "./YitFilePicker.css"
import Button from "@material-ui/core/Button";





class YitFilePicker extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentImage: '',
            isSubmitted: false,
            isSubmitting: false,
            done: false,
            partnersList: [],
            markers: []
        }

        this.stationTypes = [
            {
                label: "active",
                value: 0
            },
            {
                label: "inactive",
                value: 1
            }
        ]
        this.formRef = React.createRef()
    }

    async componentDidMount() {

    }
    onImageSelect(e){
        let Image =  URL.createObjectURL(e.target.files[0])
        let imageTag  = document.getElementById('imageTag')
        this.setState({
            currentImage: Image
        })
        imageTag.style.display = 'block'
    }


    async handleSubmit(newData){
        let password = this.generatePassword()
        alert(password)
        newData['password'] = password
        let createOp = await this.MODELS._Partner.create(newData)
        if (createOp.finalResult) {
            return true
        } else {

            this.formRef.current.setState({
                errMsg: createOp.error
            })
            return false
        }
    }

    render() {
        return (
            <div className={'YitFilePicker'}>
                <img className={"selectedImage"} src={this.state.currentImage} id={'imageTag'}/>
                <Button
                    color={"primary"}
                    variant="contained"
                    component="label"
                >
                    Select File
                    <input
                        onChange={(e)=>this.onImageSelect(e)}
                        type="file"
                        hidden
                    />
                </Button>
            </div>
        )
    }
}

export default YitFilePicker